body {
    background-color: #fff;
}

a {
    &:hover {
        text-decoration: underline;
    }
}
.page-title {
    margin-top: 100px;
    text-align: center;
    color: #424242;
    font-size: 64px;
    font-weight: 600;

    span {
        color: #40a9ff;
    }
}

main {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20vh;


    .container {
        text-align: center;
        display: flex;

        .input_search {
            width: 450px;
            border: 1px solid black;
            font-size: 18px;
            padding: 15px 30px;
            outline: none;
        }

        .options {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 20px;
            margin-left: 10px;
            select {
                width: 120px;
                padding: 6px 36px 6px 12px;
                border: 1px solid #d9d9d9;
                border-radius: 10px;
                font-size: 15px;
                outline: none;
            }

            select:focus {
                border-color: red;
            }

            button {
                width: 100px;
                height: 32px;
                background-color: #fff;
                border: 1px solid #d9d9d9;
                border-radius: 10px;
                cursor: pointer;
                outline: none;
            }

            button:hover {
                box-shadow: 0 0 0 2px rgb(24 144 255 / 20%);
                border-color: #40a9ff;
            }
        }
    }
}